import React from "react";

const Keyboard = ({ handlePower }) => {
  const row1 = [
    "esc",
    "f1",
    "f2",
    "f3",
    "f4",
    "f5",
    "f6",
    "f7",
    "f8",
    "f9",
    "f10",
    "f11",
    "f12",
    "ins",
    "prt",
    "del",
    "icon",
  ];
  const row2 = [
    ["~", "`"],
    ["!", "1"],
    ["@", "2"],
    ["#", "3"],
    ["$", "4"],
    ["%", "5"],
    ["^", "6"],
    ["&", "7"],
    ["*", "8"],
    ["(", "9"],
    [")", "0"],
    ["--", "-"],
    ["+", "="],
    ["back", ""],
    ["home", ""],
  ];
  const row3 = [
    "tab",
    "Q",
    "W",
    "E",
    "R",
    "T",
    "Y",
    "U",
    "I",
    "O",
    "P",
    "{ [",
    "} ]",
    "|",
    "pgup",
  ];
  const row4 = [
    "caps",
    "A",
    "S",
    "D",
    "F",
    "G",
    "H",
    "J",
    "K",
    "L",
    ":  ;",
    "'' '",
    "enter",
    "pgdn",
  ];
  const row5 = [
    "shift",
    "Z",
    "X",
    "C",
    "V",
    "B",
    "N",
    "M",
    "< ,",
    "> .",
    "? /",
    "shift",
    "end",
  ];
  const row6 = [
    "ctrl",
    "fn",
    "icon",
    "alt",
    "space",
    "alt",
    "ctrl",
    "<",
    "updown",
    ">",
  ];
  return (
    <div className="keyboard flex flex-col basis-3/6 w-[90%] gap-1 shadow-inner p-1">
      <div className="row1 basis-1/6 flex gap-1 justify-between">
        {row1.map((item) =>
          item === "icon" ? (
            <i
              onClick={handlePower}
              className="fa fa-power-off bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[5.4%] shadow"
              aria-hidden="true"
            ></i>
          ) : (
            <div className="bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[5.4%] shadow">
              {item}
            </div>
          )
        )}
      </div>
      <div className="row2 basis-1/6 flex justify-between gap-1">
        {row2.map((item) => (
          <div className="bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[6.2%] shadow">
            <div>{item[0]}</div>
            <div>{item[1]}</div>
          </div>
        ))}
      </div>
      <div className="row3 basis-1/6 flex justify-between gap-1">
        {row3.map((item) => (
          <div className="bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[6.2%] shadow">
            {item}
          </div>
        ))}
      </div>
      <div className="row4 basis-1/6 flex justify-between gap-1">
        {row4.map((item) =>
          item === "enter" ? (
            <div className="bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[11%] shadow">
              {item}
            </div>
          ) : (
            <div className="bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[6.2%] shadow">
              {item}
            </div>
          )
        )}
      </div>
      <div className="row5 basis-1/6 flex justify-between gap-1">
        {row5.map((item) =>
          item === "shift" ? (
            <div className="bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[13%] shadow">
              {item}
            </div>
          ) : (
            <div className="bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[6.2%] shadow">
              {item}
            </div>
          )
        )}
      </div>
      <div className="row6 basis-1/6 flex justify-between gap-1">
        {row6.map((item) =>
          item === "space" ? (
            <div className="bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[27%] shadow"></div>
          ) : item === "icon" ? (
            <i
              className="fa fa-windows bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[7.5%] shadow"
              aria-hidden="true"
            ></i>
          ) : item === "updown" ? (
            <i
              className="fa fa-angle-up bg-slate-900 hover:bg-black text-white text-xxs lg:text-xs grid place-content-center w-[7.5%] shadow"
              aria-hidden="true"
            ></i>
          ) : (
            <div className="bg-slate-900 text-white text-xxs lg:text-xs grid place-content-center w-[7.5%] shadow">
              {item}
            </div>
          )
        )}
      </div>
    </div>
  );
};

export default Keyboard;
