import { keyboard } from "@testing-library/user-event/dist/keyboard";
import React, { useState } from "react";
import BasePanel from "./BasePanel";
import TopPanel from "./TopPanel";
// import Confetti from "react-confetti";

const Laptop = () => {
  // const [celebrate, setCelebrate] = useState(false);
  {
    /* <div className="text-white w-3/4 h-[95vh] bg-yellow-400 grid place-content-center gap-4">
        <h1>hyy</h1>
        <button
          className="p-2 bg-blue-700 rounded-lg"
          onClick={() => setCelebrate(true)}
        >
          run
        </button>
        <button
          className="p-2 bg-blue-700 rounded-lg"
          onClick={() => setCelebrate(false)}
        >
          stop
        </button>
      </div>
      {celebrate && <Confetti width={1350} height={600} />} */
  }

  const [power, setPower] = useState(false);
  const [show, setShow] = useState(false);

  const handlePower = () => {
    setPower(!power);
  };

  const keyboardToggle = () => {
    const keyboard = document.querySelector(".base");
    keyboard.classList.toggle("show-keyboard");
    if (keyboard.classList.contains("show-keyboard")) {
      setShow(true);
    } else {
      setShow(false);
    }
  };

  return (
    <div className="full-screen flex sm:flex-row xs:flex-col xs:gap-12 md:gap-24 lg:gap-40 items-center justify-center hover:cursor-pointer w-screen h-screen">
      <div className="laptop-wrapper flex flex-col  xs:w-4/5 xs:h-3/4 sm:w-1/2 sm:h-[65%] md:h-3/4 lg:h-4/5 xl:h-[95vh] xl:w-[42%]">
        <TopPanel power={power} />
        <BasePanel handlePower={handlePower} />
      </div>
      <button
        onClick={keyboardToggle}
        className="text-white bg-gradient-to-br from-purple-700 to-blue-600 active:scale-110 transition ease-in duration-900 sm:w-40 hover:bg-gradient-to-bl font-semibold rounded-lg text-xl p-2 shadow-lg text-center"
      >
        {show ? "Hide" : "Show"} Keyboard
      </button>
    </div>
  );
};

export default Laptop;
